.Coupons_container {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  text-align: center;
  font-family: sans-serif;
  margin: 20px;
  padding: 20px 0px;
}

.Coupons_container:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

button{
  transition-duration: 0.5s;
}

button:hover{
  background-color: black;
  color: wheat;
  cursor: pointer;
}

.Coupons_container h1 {
  font-weight: 200;
  padding: 5px;
}

.Coupons_container p {
  padding: 5px;
}

.Coupons_container a {
  text-decoration: none;
  color: black;
  font-size: smaller;
}

.Coupons_container a:hover {
  color: #e60023;
}

@media only screen and (min-width: 700px) {
  body {
    padding: 0px;
    margin: 0px;
  }

  .Coupons {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 5% 15%;
  }

  .Coupons_container {
    flex-grow: 1;
    width: 20%;
  }
}
